.Prompt {
    background-color: #fff;
    border: 1px solid #e1e1e6;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 2rem;
    padding: 1.5rem;
}

.Prompt h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.Prompt p {
    margin-top: 0;
    margin-bottom: 0;
}

.copy-button {
    position: absolute;
    bottom: 0;
    right: 0;
    /* ... */
}

.inside-p {
    margin-bottom: 88px; /* Adjust this value to position the button inside the padding area */
    margin-right: -7px;
}

.copy-button svg {
    font-size: 1.5rem; /* Adjust the size as desired */
}

.checkmark {
    font-size: 0.9rem; /* Adjust the size as desired */
}