body {
  font-family: 'SF Pro Display', 'SF Pro Icons', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  margin: 0;
  background-color: #f8f8f8;
}

.App {
  text-align: center;
  margin: 0 auto;
  max-width: 1200px;
  padding: 2rem;
}

.App-header {
  background-color: #1d1d1f;
  color: white;
  padding: 2rem 0;
}

h11 {
  font-size: 2rem;
  font-weight: 600;
  margin: 0;
}

h22 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

h33 {
  display: block;
  font-size: 1.17em;
  margin-block-start: 0em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}


main {
  padding: 2rem 0;
}

.language-switch {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  position: absolute;
  top: 1rem;
  right: 1rem;
  outline: none;
}
